#App {
  width: 100%;
  height: 100%;
  font-size: 20px;
}

body {
  background: black;
}

a {
  color: #00aced;
  text-decoration: none;
}

.blog {
  line-height: 2rem;
}

pre {
  border-radius: 5px;
  color: rgb(62, 177, 87);
  background: rgb(41, 39, 35);
  white-space: pre-wrap;
  line-height: 1em;
}

.blog img {
  display: block;
  width: 80%;
  border-radius: 15px;
}

#twitter-bot-blog img {
  width: 20%;
}

#content {
  width: 70%;
  margin: auto;
}

.section .blog ul {
  margin-left: 50px;
}

.blog .blog-section {
  margin-bottom: 30px;
}

.section {
  padding: 20px;
  margin-bottom: 10px;
}

#projects {
}

#about #info {
  width: 70%;
  line-height: 1.8rem;
}

#contact ul {
  list-style: none;
  margin: 20px;
}

#switch-theme {
  position: fixed;
  right: 0;
  padding: 5px;
  border: none;
  margin: 10px;
}