.project-listing {
  width: 90%;
  height: auto;
  margin: auto;
  margin-bottom: 10px;
  margin-top: 10px;
  background: rgb(231, 231, 231);
  border-radius: 12px;
  padding: 10px;
}

.project-listing .project-preview-content {
  display: inline-block;
  vertical-align: middle;
  width: 50%;
}

.project-listing .project-desc {
  font-family: 'Courier New', Courier, monospace
}

.project-listing .thumbnail {
  width: 9rem;
  height: 9rem;
  margin: 10px;
  vertical-align: middle;
  border-radius: 5px;
}

.project-listing .project-title {
  overflow-wrap: normal;
}

.project-listing .project-expand {
  display: inline-block;
  margin-top: 10px;
  border: none;
  background: #cccccc;
  padding: 10px;
  border-radius: 3px;
}